<template>
  <div>
      <van-nav-bar title="黑马头条-登录"/>
      <van-form @submit="onSubmit">
        <van-field
          v-model="user.mobile"
          required
          name="mobile"
          label="手机号"
          placeholder="请输入11位手机号"
          :rules="[{ required: true, message: '请填写正确手机号',pattern:/^1[3-9]\d{9}$/}]"
        />
        <van-field
          v-model="user.code"
          required
          type="password"
          name="code"
          label="密码"
          placeholder="请输入6位密码"
          :rules="[{ required: true, message: '请填写密码', pattern:/^\d{6}$/}]"
        />
<div style="margin: 16px">
          <!-- round是否为圆形
              (1): 属性后面无值, 默认值为true
              block代表块级元素
              type代表类型(有默认颜色)
              native-type代表原生button的type属性
              代表这个van-button组件渲染的原生button标签type是submit提交整个表单功能的按钮
            -->
          <van-button round block type="info" native-type="submit"
          :disabled="isLoading"
          :loading="isLoading"
          loading-text="正在登录ing..."
            >登录</van-button
          >
        </div>
</van-form>
  </div>
</template>

<script>
import { loginAPI } from '@/api'
import { Notify } from 'vant'
import { setToken } from '@/utils/token.js'
export default {
  data () {
    return {
      user: {
        mobile: '13888888888', // 手机号
        code: '246810'
      },
      isLoading: false
    }
  },
  methods: {
    async onSubmit (values) {
      console.log('submit', values)
      console.log(this.user)
      this.isLoading = true
      try {
        const res = await loginAPI(this.user)
        console.log(res)
        Notify({ type: 'success', message: '登录成功啦！！！哈哈哈' })
        setToken(res.data.data.token)
        this.$router.replace({
          path: this.$route.query.path || '/layout/home'
        })
      } catch (err) {
        Notify({ type: 'danger', message: '账号或密码错误，噢噢哦' })
      }
      this.isLoading = false
    }

    // methods: {

  //   async onSubmit () {
  //     try {
  //       const res = await loginAPI(this.formLogin)
  //       const { token, refresh_token } = res.data.data
  //       console.log(token)
  //       console.log(refresh_token)
  //     } catch (err) {
  //       console.error(err)
  //     }
  //   }
  // }
  }
}
</script>

<style scoped >
   .van-nav-bar{
     background-color: #007bff;
   }
   /* .van-icon .van-icon-arrow-left .van-nav-bar__arrow{
     color: white;
   } */
</style>
